<template>
  <div>
    <v-dialog
      :value="show"
      max-width="350"
      @click:outside="$emit('close')"
    >
      <v-card>
        <v-card-title class="headline">Melde Dich an</v-card-title>

        <div class="container">
          <v-tabs v-model="tab" grow color="success">
            <!-- LOGIN -->
            <v-tab color="success">Anmelden</v-tab>

            <!-- REGISTER -->
            <v-tab>Registrieren</v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <!-- LOGIN -->
            <v-tab-item>
              <v-card flat>
                <v-form>
                  <v-container>
                    <v-text-field
                      color="success"
                      v-model.lazy="login.mail"
                      :rules="rules.requiredString50Mail"
                      :counter="50"
                      label="E-Mail"
                    />
                    <v-text-field
                      color="success"
                      v-model.lazy="login.password"
                      :rules="rules.requiredString50"
                      :counter="50"
                      type="password"
                      label="Passwort"
                    />
                  </v-container>
                </v-form>
              </v-card>
            </v-tab-item>

            <!-- REGISTER -->
            <v-tab-item>
              <v-card flat>
                <v-form>
                  <v-container>
                    <v-text-field
                      color="success"
                      v-model.lazy="register.username"
                      :rules="rules.requiredString50Mail"
                      :counter="50"
                      label="E-Mail"
                    />
                    <v-text-field
                      color="success"
                      v-model.lazy="register.password"
                      :rules="rules.requiredString50"
                      :counter="50"
                      type="password"
                      label="Passwort"
                    />
                    <v-text-field
                      color="success"
                      v-model.lazy="register.passwordRepeat"
                      :rules="rules.requiredString50"
                      :counter="50"
                      type="password"
                      label="Passwort wiederholen"
                    />
                  </v-container>
                </v-form>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
        </div>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn v-if="tab === 0"
            color="green darken-1"
            text
            @click="$emit('close')"
          >
            Anmelden
          </v-btn>

          <v-btn v-if="tab === 1"
            color="green darken-1"
            text
            @click="$emit('close')"
          >
            Registrieren
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="$emit('close')"
          >
            Abbrechen
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'auth-dialog',
  props: {
    show: Boolean
  },
  data: () => ({
    tab: 0,
    login: {
      mail: '',
      password: ''
    },
    register: {
      mail: '',
      password: '',
      passwordRepeat: ''
    },
    rules: {
      requiredString50Mail: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 50 || ' ',
        v => /\S+@\S+\.\S+/.test(v) || 'Keine gltige E-Mail'
      ],
      requiredString50: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 50 || ' '
      ],
    }
  })
}
</script>

